.header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    z-index: 1;
    }

.header__wrap {
    background-image: linear-gradient(180deg, #000000 0%, rgba(0, 0, 0, 0.55) 54.17%, rgba(0, 0, 0, 0) 100%);
    margin: 0 auto;

    @media screen and (max-width: 1023px) {
        max-width: 414px;
        height: 143px;
    }

    @media screen and (min-width: $tablet) {
        max-width: 1024px;
        height: 116px;
    }

    @media screen and (min-width: $desktop) {
        max-width: 1280px;
        height: 147px;
    }
}
.header__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media screen and (min-width:$tablet) and (max-width:1279px) {
        gap: 87px;
    }

    @media screen and (min-width: $desktop) {
        justify-content: left;
    }
}

.header__logo {
    text-decoration: none;
    display: block;
    font-family: 'Film P3', sans-serif;
    font-weight: 700;
    font-size: 36px;
    line-height: 1.08;
    letter-spacing: 0.3em;
    color: $white;

    @media screen and (max-width:1023px) {
        max-width: 114px;
        padding-top: 16px;
        padding-bottom: 9px;
    }

    @media screen and (min-width:$tablet) {
        max-width: 127px;
        font-size: 40px;
        line-height: 1.08;
    }

    @media screen and (min-width:$desktop) {
        margin-right: 96px;
    }
}

.navigation__list {
    display: flex;

    @media screen and (max-width:1023px) {
        justify-content: space-between;
        gap: 20px;
    }

    @media screen and (min-width:$tablet) {
        gap: 72px;
    }

    @media screen and (min-width:$desktop) {
        margin-right: 59px;
    }
}

.navigation__item {
    display: flex;
    align-items: center;

    @media screen and (max-width:1023px) {
        padding-top: 28px;
        padding-bottom: 12px;

    }

    @media screen and (min-width:$tablet) {
        padding: 30px 0;
    }
}

.navigation__link {

    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.25;
    color: $accent;
    text-decoration: none;
position: relative;

    &::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 3px;
    left: 0;
    bottom: 0;
    background:rgba(243, 198, 83, 0.8);
    border-radius: 3px;
    transform: scaleX(0);
}

    &:hover::after,
    &:focus::after{
    transform: scaleX(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    @media screen and (max-width:1023px) {
        margin-right: 6px;
    }

    @media screen and (min-width:$tablet) {
        margin-right: 32px;
        font-size: 20px;
        line-height: 1.2;
        padding: 8px 0;
    }
}

.navigation__box {
    display: flex;
    align-items: center;
    white-space: nowrap;

    @media screen and (min-width:$tablet) {
        border: 1px solid rgba(243, 198, 83, 0.7);
        border-radius: 2px;
        padding: 8px;
    }
}

.navigation__contacts {
    display: flex;
    align-items: center;
}


.navigation__icon {
    fill: $accent;
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    .navigation__contacts:hover &,
    .navigation__contacts:focus & {
        transform: scale(1.2);
    }

    @media screen and (min-width:$tablet) {
        margin-right: 8px;
    }
}
.navigation__phone {
    text-decoration: none;
    align-items: center;
    transition: text-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover, &:focus{
text-shadow: 0.5px 0.5px 0.5px $white, 0 0 0.5em $primary-bg, 0 0 0.1em $primary-bg;
    }
    

    @media screen and (max-width:1023px) {
        display: none;
    }

    @media screen and (min-width:$tablet) {
        display: flex;
        font-weight: 400;
        font-size: 14px;
        line-height: 1.21;
        color: $white;
    }
}

.navigation__phone--first {
    margin-right: 16px;
}

.header__networks {
    display: none;

    @media screen and (min-width:$desktop) {
        display: block;
    }
}

.networks__list {
    display: flex;
    gap: 20px;
    width: 112px;
    height: 24px;
}

.networks__icon {
    fill: rgba(243, 198, 83, 0.7);
    transform: scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

    .networks__link:hover &,
    .networks__link:focus &  {
        transform: scale(1.2);
    }
}